import React from 'react'
import { Grid,Paper, TextField, Button, Typography,Link } from '@material-ui/core'
import { useState } from 'react';
import axios from 'axios'

const Invest = () => {
  const url = '/api/invest'
  const [data, setData] = useState({
    name: "",
    phone: "",
    promo: ""
  })
  const handle = (e) => {
    const newData = {...data}
    console.log(newData)
    newData[e.target.id] = e.target.value
    setData(newData)
    console.log(newData)
  }
  const isFilled = (string) => {
    if (string.trim().length === 0) {
      return false
    } else {
      return true
    }
  }
  const submitData = (e) => {
    if (isFilled(data.name) && isFilled(data.phone) && isFilled(data.promo)) {
      axios.post(url, {
        name: data.name,
        phone: data.phone,
        promo: data.promo
      })
        .then(res => {
          console.log(res)
          alert("  !")
        })
        .catch(err => console.log(err))
    } else {
      alert("  ")
    }
  }
  const paperStyle={padding :20,height:'35vh',width:600, margin:"20px auto"}
  const btnstyle={margin:'8px 0'}
  return (
    <div>
      <br/>
      <br/>
        <Grid>
            <Paper elevation={10} style={paperStyle}>
                <Grid align='center'>
                    <h2 style={{fontWeight: 'bold'}}>   </h2>
                </Grid>
                <TextField onChange={(e) => handle(e)} value={data.name} id="name" label='' placeholder=' ' fullWidth required/>
                <TextField onChange={(e) => handle(e)} value={data.phone} id="phone" label='' placeholder='  ' fullWidth required/>
                <TextField onChange={(e) => handle(e)} value={data.promo} id="promo" label='' placeholder='  ( )' fullWidth/>
                <Button onClick={submitData} type='submit' color='primary' variant="contained" style={btnstyle} fullWidth> </Button>
                <Typography >
                    ,   <br/> <Link href="#">  </Link>  <Link href="#"> </Link>
                </Typography>
            </Paper>
        </Grid>
  </div>
  )
}

export default Invest